import React, { useMemo } from 'react'
import { graphql } from 'gatsby'
import { getImage } from 'gatsby-plugin-image'
import Banner from '../../containers/HomePage/Banner'

const Desktop = ({ data: { allDesktopJson: { nodes } } }) => {
  const images = useMemo(() => nodes.map(layer => ({
    ...layer,
    image: getImage(layer.image),
  })).reverse(), [nodes])

  return (
    <Banner
      width={2560}
      height={1140}
      images={images}
    />
  )
}

export default Desktop

export const query = graphql`
query DesktopBannerQuery {
  allDesktopJson {
    nodes {
      name
      width
      height
      x
      y
      depth
      image {
        childImageSharp {
          gatsbyImageData(
            layout: FULL_WIDTH
          )
        }
      }
    }
  }
}
`
